"use strict";
var accordion_1 = require("./accordion");
(function ($, window, document) {
    var Plugin = (function () {
        function Plugin(element, options) {
            this.defaults = {
                option1: 'change this'
            };
            this.element = element;
            this.$element = $(element);
            this.options = $.extend({}, this.defaults, options);
            this.init();
        }
        Plugin.prototype.init = function () {
            var _this = this;
            console.log('init typescript demo plugin');
            var accordion = new accordion_1.default('Hello Accordion');
            accordion.greet();
            this.$element
                .html('TypeScript Plugin loaded')
                .css('backgroundColor', 'lightcoral')
                .on('click', function () {
                console.log('clicked element');
                _this.$element.css('backgroundColor', 'green');
            });
        };
        return Plugin;
    }());
    Plugin.NAME = 'accordion';
    $.fn[Plugin.NAME] = function (options) {
        return this.each(function () {
            var $this = $(this);
            if (!$this.data(Plugin.NAME)) {
                $this.data(Plugin.NAME, new Plugin(this, options));
            }
        });
    };
})(jQuery, window, document);



